import {
	mySuggest,
	deleteSuggest
} from "../../../api/user.js"
export default {
	data() {
		return {
			page: 1,
			isShowLoading: true,
			scrollNodata: false,
			isLoadMore: true,
			list: [],
			isReload:true
		}
	},
	onLoad() {
		uni.hideShareMenu() 
	},
	onReachBottom() {
		this.getMysuggest()
	},
	onUnload() { 
		this.isReload = true
	},
	onShow() { 
	},
	mounted() {
		this.getMysuggest()
	},
	methods: {
		getMysuggest() {
			if (!this.isLoadMore) return
			this.isLoadMore = false
			mySuggest({
				data: {
					page_no: this.page + "",
					page_size: '10'
				}
			}).then(res => {
				if (res.data.code == 0) {
					let list = res.data.result.items
					list = list.map(item=>{ 
						item.images = item.images!='' ? item.images.split(',') : [] 
						return item
					})
					if (list.length == 10) {
						this.isLoadMore = true
						this.page++
					}
					if (list.length < 10) {
						this.isShowLoading = false
					}
					if (list.length < 10 && this.page > 1) {
						this.scrollNodata = true
					}
					this.list = [...this.list, ...list]
				}
			})
		},
		// 编辑跳转
		editSuggest(id){
			uni.navigateTo({
				url:`/pages/user/submit-suggest/submit-suggest?fid=${id}`
			})
		},
		// 删除建议
		delSuggest(id,index){
			let that = this;
			
			uni.showModal({
				confirmColor:"#C9AD94",
				title:"删除提醒",
				content:"确定删除此条反馈意见吗？",
				success(r) {
					if(r.confirm) {
						uni.showLoading({
							title:'删除中...',
							mask:true
						})
						deleteSuggest({
							data:{
								feedback_id:id
							}
						}).then(res=>{
							uni.hideLoading()
							if(res.data.code == 0) {
								uni.showToast({
									title:"删除成功",
									icon:"none"
								})
								that.list.splice(index,1)
							}
						})
					}
				}
			}) 
		},
		// 预览
		preView(src,idx){
			let that = this
			uni.previewImage({
				current:src,
				urls:this.list[idx].images,
				success() {
					// that.isReload = false
				}
			})
		}
	}
}
